
import { defineComponent } from 'vue';
// import I

export default defineComponent({
  name: 'Home',
  data() {
    return {
      languages: ['en'],
    };
  },
  mounted() {
    this.languages = this.$i18n.availableLocales;
  },
  methods: {
    setLanguage(lang: string) {
      this.$i18n.locale = lang;
    },
  },
});
